import React from 'react';
import { MoreHorizontal } from 'lucide-react';

interface InfrastructureAgentProps {
  activeTab: string;
}

const InfrastructureAgent: React.FC<InfrastructureAgentProps> = ({ activeTab }) => {
  const getTabContent = () => {
    switch (activeTab) {
      case 'create':
        return {
          number: '01/03',
          title: 'Infrastructure Agent',
          description: 'Deploy and scale servers, storage, and networks.'
        };
      case 'manage':
        return {
          number: '02/03',
          title: 'Operations Manager',
          description: 'Monitor, maintain, and optimize your infrastructure.'
        };
      case 'optimise':
        return {
          number: '03/03',
          title: 'Performance Optimizer',
          description: 'Analyze and enhance system performance and costs.'
        };
      default:
        return {
          number: '01/03',
          title: 'Infrastructure Agent',
          description: 'Deploy and scale servers, storage, and networks.'
        };
    }
  };

  const content = getTabContent();

  return (
    <div className=" p-8    flex flex-col justify-center min-h-[600px]">
      <div>
        <div className="text-sm text-gray-300 mb-2">{content.number}</div>
        <h2 className="text-2xl font-bold mb-4">{content.title}</h2>
        <p className="text-gray-300 mb-8 leading-relaxed">
          {content.description}
        </p>
      </div>
      
      <button className="flex items-center gap-3 px-6 py-3 border border-white border-opacity-30 rounded-full hover:bg-white hover:bg-opacity-10 transition-colors">
        <span className="font-medium tracking-wide">LEARN MORE</span>
        <MoreHorizontal className="w-5 h-5" />
      </button>
    </div>
  );
};

export default InfrastructureAgent;